import React from 'react'
import { Link, useLocation, useNavigate} from 'react-router-dom'
import Swal from 'sweetalert2'; 

const Navbar = () => {
   let location = useLocation();
   let navigate = useNavigate();

   const handleLogout = () => {
  localStorage.removeItem('token');
  localStorage.removeItem('user');

  Swal.fire({
    icon: 'success',
    title: 'Logged Out',
    text: 'You have been logged out successfully!'
  }).then(() => {
    navigate('/login'); // Redirect after SweetAlert is dismissed
  });
};

  return (
    <div>
      <nav  className="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
            <div className="container-fluid">
                <Link className="navbar-brand" to="/"> <img src="/logo.png" alt="iNotebook Logo" height="30" />  iNotebook</Link>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link className={`nav-link ${location.pathname==="/"? "active": ""}`} aria-current="page" to="/">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link className={`nav-link ${location.pathname==="/about"? "active": ""}`} to="/about">About</Link>
                        </li>

                    </ul>
                   { !localStorage.getItem('token') ? <form className="d-flex"> 
                    <Link className="btn btn-primary mx-1" to="/login" role="button">Login</Link>
                    <Link className="btn btn-primary mx-1" to="/signup" role="button">Signup</Link>
                    </form>: 
                    <button className="btn btn-primary mx-1" onClick={handleLogout} >Logout</button>
                }
                </div>
            </div>
        </nav>
    </div>
  )
}

export default Navbar

